import CONFIG from 'assets/config/server.config';
import HttpRequest from 'core/networking/http';

export class StatisticsService {
  #engine = new HttpRequest(CONFIG.CreatePath('admin/statistics'));
  constructor(token) {
    this.#engine.SetHeader('test', 'active');
    this.#engine.SetHeader('authorization', token);
    this.#engine.SetHeader('token', token);
    StatisticsService.instance = this;
  }

  /**
   * @type { StatisticsService }
   */
  static instance;

  async GetUsersStatistics() {
    try {
      const response = await this.#engine.Post('/users/count', {});
      return response.body;
    } catch (e) {
      console.log(e);
      return 0;
    }
  }
  async GetShopStatistics() {
    try {
      const response = await this.#engine.Post('/shop', {});
      return response.body;
    } catch (e) {
      console.log(e);
      return 0;
    }
  }
  /**
   *
   * @param {Array<{
   *      _id: String,
   *      Amount: number,
   *      Authority: String,
   *      Date: String,
   *      ProductId: {
   *         _id:  String,
   *         Name: String,
   *      },
   *      _id: String,
   *      Name: String,
   *      StatusCode: number,
   *      UserId: {
   *         _id: String,
   *         UserName: String
   *      },
   *      _id: String,
   *      UserName: String
   *  }>} statistics
   */
  shopStatisticsService(statistics) {
    const _self = {
      getBasic: () => {
        const response = statistics;
        const data = [];
        const amounts = {};
        for (const item of response) {
          const date = new Date(item.Date);
          const key = `${date.getFullYear()}/${date.getMonth()}/${date.getDate()}`;
          if (!amounts[key]) amounts[key] = 0;
          amounts[key] += item.Amount;
        }
        for (const i in amounts) {
          data.push({
            date: i,
            'مقدار فروش': amounts[i]
          });
        }

        let myState = data;
        if (data.length === 1) {
          myState = [
            {
              date: '0',
              'مقدار فروش': 0
            },
            ...data
          ];
        }

        return myState;
      },

      /**
       *
       * @param {Date} from
       * @param {Date} to
       */
      getRange: (from, to) => {
        const response = statistics.filter((i) => {
          const date = new Date(i.Date);
          return date >= from && date <= to;
        });

        const data = [];
        const amounts = {};
        console.log(response);
        for (const item of response) {
          const date = new Date(item.Date);
          const newKey = `${date.getFullYear()}/${date.getMonth()}/${date.getDate()}`;
          if (!amounts[newKey]) amounts[newKey] = 0;
          amounts[newKey] += item.Amount;
        }
        console.log(amounts);
        for (const i in amounts) {
          data.push({
            date: i,
            'مقدار فروش': amounts[i]
          });
        }
        return data.length === 1
          ? [
              {
                date: '0',
                'مقدار فروش': 0
              },
              ...data
            ]
          : data;
      },

      /**
       *
       * @param {String} key
       */
      getFromDate: (key) => {
        const response = statistics.filter((item) => {
          const date = new Date(item.Date);
          const k = `${date.getFullYear()}/${date.getMonth()}/${date.getDate()}`;
          return k === key;
        });
        const data = [];
        const amounts = {};
        for (const item of response) {
          const date = new Date(item.Date);
          const newKey = `${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}`;
          amounts[newKey] = item.Amount;
        }
        for (const i in amounts) {
          data.push({
            date: i,
            'مقدار فروش': amounts[i]
          });
        }

        return data.length === 1
          ? [
              {
                date: '0',
                'مقدار فروش': 0
              },
              ...data
            ]
          : data;
      },

      getMostSelling: () => {
        const data = [];
        for (const item of statistics) {
          const index = data.findIndex((row) => row.id === item.ProductId._id);
          if (index === -1) {
            data.push({
              id: item.ProductId._id,
              date: item.ProductId.Name,
              amount: item.Amount
            });
            continue;
          }
          data[index].amount += item.Amount;
        }
        const sorted = data.map((item) => ({
          id: item.id,
          date: item.date,
          'مقدار فروش': item.amount
        }));
        return sorted;
      },

      getProfitAmount: () => {
        const sells = _self.getBasic().map((i) => i['مقدار فروش']);
        const [beforeLatest, latest] = [sells[sells.length - 2], sells[sells.length - 1]];

        const totalProfit = sells.reduce((p, c) => p + c, 0);
        const amount = (latest - beforeLatest).toFixed(0);
        const precentage = ((amount * 100) / totalProfit).toFixed(1);

        return {
          amount,
          precentage
        };
      },

      getMostSellingPlatform: () => {
        const _object = {};
        for (const factor of statistics) {
          const auth = factor.Authority;
          const platform = auth.split('@')[0];
          if (!_object[platform]) _object[platform] = 0;
          _object[platform] += factor.Amount;
        }

        const data = [];
        for (const platform in _object) {
          const obj = {
            platform: platform,
            sellAmount: _object[platform]
          };
          if (platform === 'MYKET') obj.platform = 'مایکت';
          else if (platform === 'BAZZAR') obj.platform = 'بازار';
          else obj.platform = 'زرین پال';
          data.push(obj);
        }
        return data;
      },

      getMostFactorCountPlatform: () => {
        const _object = {};
        for (const factor of statistics) {
          const auth = factor.Authority;
          const platform = auth.split('@')[0];
          if (!_object[platform]) _object[platform] = 0;
          _object[platform] += 1;
        }

        const data = [];
        for (const platform in _object) {
          const obj = {
            platform: platform,
            sellAmount: _object[platform]
          };
          if (platform === 'MYKET') obj.platform = 'مایکت';
          else if (platform === 'BAZZAR') obj.platform = 'بازار';
          else obj.platform = 'زرین پال';
          data.push(obj);
        }

        return data;
      },

      getFullSellAmount: () => {
        return statistics.reduce((a, b) => a + b.Amount, 0);
      }
    };

    return _self;
  }

  /**
   *
   * @param { {
   *  OnlineUsersCount: number,
   *  UsersCount: {
   *    NumberOfRegistrationsByDate: object,
   *    NumberOfUserInvited: Array<{
   *      _id: String,
   *      AdvertiseStatus: boolean,
   *      InviteCount: number,
   *      Vip: boolean
   *    }>
   *  }
   * } } statistics
   */
  usersStatisticsService(statistics) {
    return {
      getUsersCount: () => {
        return statistics.UsersCount.NumberOfUserInvited.length;
      },
      getOnlineCount: () => statistics.OnlineUsersCount
    };
  }
}

export default StatisticsService;
