import React, { Component } from 'react';
import SideNav from 'components/NavigationBars/SideNav';
import { SidenavItems } from 'components/NavigationBars/SideNav';
import HomeIcon from '@mui/icons-material/HomeRounded';
import CubeIcon from '@mui/icons-material/ViewInArRounded';
import LocalOfferRoundedIcon from '@mui/icons-material/LocalOfferRounded';
import DashboardRouterModule from './dashboard.router.module';
import {
  Badge,
  Button,
  CircularProgress,
  Fab,
  ListItemIcon,
  ListItemText,
  MenuItem,
  ThemeProvider,
  Typography
} from '@mui/material';
import UserIcon from '@mui/icons-material/PersonRounded';
import { theme as __theme__, themeRaw, darkMode } from 'utils/colorPallette';
import { createTheme } from '@mui/material/styles';
import { DashboardService } from './dashboard.service';
import './dashboard.style.scss';
import { connect } from 'react-redux';
import SettingsIcon from '@mui/icons-material/SettingsRounded';
import AuthGuardProvider from 'guards/auth';
import ChartIcon from '@mui/icons-material/ShowChartRounded';
import NotificationIcon from '@mui/icons-material/NotificationsTwoTone';
import DarkModeIcon from '@mui/icons-material/Brightness6TwoTone';
import LogoutIcon from '@mui/icons-material/LogoutRounded';
import Menu from '@mui/material/Menu';
import AssignmentRoundedIcon from '@mui/icons-material/AssignmentRounded';
import Logo from 'assets/Logo.png';
import EnvlopeIcon from '@mui/icons-material/EmailRounded';
import AdminPanelSettingsTwoToneIcon from '@mui/icons-material/AdminPanelSettingsRounded';
// Dialogs
import Dialog from '@mui/material/Dialog';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import SettingActions from 'db/setting/actions';
import USER_ACTIONS from 'db/user/action';

import MessagesWidget from './@Widgets/message';
import { TasksService } from './Tasks/tasks.service';
import MESSAGES_ACTIONS from 'db/messages/actions';
import TOKEN_ACTIONS from 'db/token/action';
import axios from 'axios';
import HttpRequest from 'core/networking/http';

class DashBoardModule extends Component {
  #service = new DashboardService();
  constructor(props) {
    super(props);
    const storageTheme = localStorage.getItem('theme');
    if (storageTheme !== (this.props.setting.darkMode ? 'dark' : 'light')) {
      this.props.setting.darkMode = storageTheme === 'dark';
      this.props.dispatch({
        type: SettingActions.SET_DARK_MODE,
        payload: this.props.setting.darkMode
      });
    }
    this.state = {
      LoadDashboard: false,
      loginDialog: false,
      username: '',
      password: '',
      error: null,
      disable: false,
      myTheme: createTheme(this.props.setting.darkMode ? darkMode : themeRaw),
      menuItem: null,
      notReadMessagesLen: 0,
      notifications: null
    };
    if (this.props.setting.darkMode) {
      this.state.myTheme.palette.mode = 'dark';
      this.state.myTheme = createTheme(darkMode);
      document.body.className += ' bootstrap-dark ';
    } else {
      this.state.myTheme = createTheme(themeRaw);
      this.state.myTheme.palette.mode = 'light';
      this.state.myTheme.palette.text.secondary = '#555';
      document.body.className.replace('bootstrap-dark', '');
      document.body.className += ' bootstrap ';
    }
    document.body.setAttribute('theme', this.state.myTheme.palette.mode);
    this.#service.SetStore(props.dispatch);
    console.log(props);
  }

  componentDidMount() {
    this.#service.StoreShopDataAsync(this.#service.GetShopData()).then(() => {
      this.setState({
        LoadDashboard: true
      });
    });
    if (!this.props.user || !Object.keys(this.props.user).length) {
      console.log('loading user');
      this.props.dispatch({
        type: USER_ACTIONS.LOAD
      });
    }
    setTimeout(() => {
      this.messagesCount();
    }, 500);
  }

  messagesCount = () => {
    const seenMessages =
      JSON.parse(localStorage.getItem(this.props.user.UserId + '@seenMessages')) || [];
    if (!this.props.messages.length) {
      const srv = !TasksService.instance
        ? new TasksService(this.props.token)
        : TasksService.instance;
      srv.getAll().then((r) => {
        if (r.status === 1) {
          this.props.dispatch({
            type: MESSAGES_ACTIONS.SET,
            payload: r.data
          });

          const messages = r.data.filter(
            (i) => i.Status === 4 && i.Creator._id !== this.props.user.UserId
          );

          this.setState({
            notReadMessagesLen: messages.length - seenMessages.length
          });
        }
      });
    } else {
      const messages = this.props.messages.filter(
        (i) => i.Status === 4 && i.Creator._id !== this.props.user.UserId
      );
      this.setState({
        notReadMessagesLen: messages.length - seenMessages.length
      });
    }
  };

  componentDidUpdate() {
    const v = this.props.setting.darkMode ? 'dark' : 'light';
    if (document.body.getAttribute('theme') === v) return;
    else {
      document.body.setAttribute('theme', v);
      const t = v === 'dark' ? createTheme(darkMode) : createTheme(themeRaw);
      this.setState(
        {
          myTheme: t
        },
        () => {
          if (this.props.setting.darkMode) {
            document.body.className += ' bootstrap-dark ';
          } else {
            document.body.className.replace('bootstrap-dark', '');
          }
        }
      );
    }
  }

  onMenuClosed = () => {
    this.setState({
      menuItem: null
    });
  };
  onNotificationsMenuClosed = () => {
    this.setState({
      notifications: null
    });
  };

  onLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    this.props.dispatch({
      type: TOKEN_ACTIONS.SET,
      payload: 'none'
    });
    this.props.dispatch({
      type: USER_ACTIONS.SET,
      payload: {}
    });
    this.props.dispatch({
      type: MESSAGES_ACTIONS.SET,
      payload: []
    });
    axios.defaults.headers.common['authorization'] = '';
    HttpRequest.ClearAllAuthorizations();
    this.props.redirect('/');
  };
  render() {
    return (
      <>
        <ThemeProvider theme={this.state.myTheme}>
          <SideNav>
            <SidenavItems to={'home'} Icon={HomeIcon} Name={'داشبورد'} />
            <SidenavItems to={'files'} Icon={CubeIcon} Name={'فایل ها'} />
            <SidenavItems to={'shop'} Icon={LocalOfferRoundedIcon} Name={'فروشگاه'} />
            <SidenavItems to={'statistics'} Icon={ChartIcon} Name={'آمار '} />
            <SidenavItems to={'setting'} Icon={SettingsIcon} Name={'تنظیمات'} />
            <SidenavItems to={'admins'} Icon={AdminPanelSettingsTwoToneIcon} Name={'ادمین ها'} />
            <SidenavItems to={'tasks'} Icon={AssignmentRoundedIcon} Name={'تسک ها'} />
          </SideNav>

          <Menu
            id="user-menu"
            anchorEl={this.state.menuItem}
            open={Boolean(this.state.menuItem)}
            className="rounded-pill"
            onClose={this.onMenuClosed}>
            <MenuItem onClick={() => this.props.redirect('./profile/')}>
              <ListItemIcon>
                <UserIcon fontSize="small" />
              </ListItemIcon>
              <ListItemText>پروفایل</ListItemText>
            </MenuItem>
            <MenuItem onClick={() => this.props.redirect('./profile/messages')}>
              <ListItemIcon>
                <EnvlopeIcon fontSize="small" />
              </ListItemIcon>
              <ListItemText>پیام ها</ListItemText>
            </MenuItem>
          </Menu>
          <Menu
            id="notifications-menu"
            anchorEl={this.state.notifications}
            open={Boolean(this.state.notifications)}
            className="rounded-pill"
            onClose={this.onNotificationsMenuClosed}>
            <MessagesWidget
              id={this.props.user.UserId}
              onSelect={(id) => {
                this.props.redirect('./tasks/message/' + id);
              }}
              messages={this.props.messages.filter(
                (i) => i.Status === 4 && i.Creator._id !== this.props.user.UserId
              )}
            />
          </Menu>
          <div className="row">
            <div className="col-12">
              <div className="row mx-3 mx-sm-5 mt-2">
                <div className="col-2"></div>
                <div className="col-lg-10 text-end options text-white rounded p-2 px-3">
                  <Fab
                    onClick={(e) => {
                      this.setState({
                        menuItem: e.currentTarget
                      });
                    }}
                    className="item shadow-lg"
                    color="inherit"
                    variant="extended">
                    <UserIcon />
                    <span className="mx-2">{this.props.user.UserName || 'ادمین'}</span>
                  </Fab>
                  <Fab
                    onClick={(e) => {
                      this.setState({
                        notifications: e.currentTarget,
                        notReadMessagesLen: 0
                      });
                    }}
                    className="item shadow-lg mx-2"
                    color="inherit"
                    size="small">
                    {!this.state.notReadMessagesLen ? (
                      <NotificationIcon />
                    ) : (
                      <Badge badgeContent={this.state.notReadMessagesLen} color="secondary">
                        <NotificationIcon />
                      </Badge>
                    )}
                  </Fab>
                  <Fab
                    onClick={() => {
                      this.props.dispatch({
                        type: SettingActions.SET_DARK_MODE,
                        payload: !this.props.setting.darkMode
                      });
                    }}
                    className="item shadow-lg mx-2"
                    color="inherit"
                    size="small">
                    <DarkModeIcon />
                  </Fab>
                  <Fab
                    onClick={this.onLogout}
                    className="item shadow-lg mx-2"
                    color="inherit"
                    size="small">
                    <LogoutIcon />
                  </Fab>
                </div>
              </div>
              <div className="row mx-3 mt-1 mx-sm-5 pt-3">
                <div className="col-2"></div>
                <div
                  className="col-lg-10 rounded  dashboard-paper shadow-lg p-2"
                  style={{
                    overflowX: 'hidden',
                    overflowY: 'auto',
                    minHeight: '60vh',
                    // maxHeight: '80vh',
                    backgroundColor: this.state.myTheme.palette.background.paper,
                    color: this.state.myTheme.palette.text.primary,
                    transition: '0.25s ease'
                  }}>
                  <Dialog open={this.state.loginDialog} fullWidth>
                    <DialogTitle className="text-center">لطفا دوباره وارد شوید</DialogTitle>
                    <DialogContent>
                      <div className="row">
                        <div className="col-12 text-center">
                          <Button
                            onClick={() => {
                              this.props.redirect('/');
                            }}>
                            {' '}
                            بازگشت به صفحه ورود{' '}
                          </Button>
                        </div>
                      </div>
                    </DialogContent>
                  </Dialog>
                  <AuthGuardProvider
                    authorizationFailed={() => {
                      this.setState({
                        loginDialog: true
                      });
                    }}>
                    {this.state.LoadDashboard ? (
                      <DashboardRouterModule baseRoute="dashboard" />
                    ) : (
                      <CircularProgress />
                    )}
                  </AuthGuardProvider>
                </div>
              </div>
              <div className="row mx-3 mt-1 mx-sm-5 pt-3">
                <div className="col-2"></div>
                <div
                  className="col-lg-10 border-top p-2"
                  style={{
                    overflowX: 'hidden',
                    overflowY: 'auto',
                    // maxHeight: '80vh',
                    color: this.state.myTheme.palette.text.primary,
                    transition: '0.25s ease'
                  }}>
                  <div className="row">
                    <div className="col-12 text-center">
                      <img src={Logo} width={50} className="mx-auto" height={50} alt="logo" />
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-12 text-center">
                      <Typography align="center" dir="ltr" variant="caption">
                        © RedTree Std. Iran Shiraz
                      </Typography>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </ThemeProvider>
      </>
    );
  }
}

export const theme = __theme__;
const mapStateToProps = (state) => state;
export const DashBoard = connect(mapStateToProps)(DashBoardModule);
export default DashBoard;
