import PRODUCTS_ACTIONS from './actions';
const initialValue = [];

export function ProductsReducer(state = initialValue, action) {
  switch (action.type) {
    case PRODUCTS_ACTIONS.SET:
      if (typeof action.payload === typeof initialValue && action.payload instanceof Array) {
        state = action.payload;
      }
      return state;
    case PRODUCTS_ACTIONS.PUSH:
      state.push(action.payload);
      return state;
    default:
      return state;
  }
}
