import CONFIG from 'assets/config/server.config';
import { LoginBody } from 'core/models/app/Login.models';
import HttpRequest from 'core/networking/http';
import { Buffer } from 'buffer';

export class AdminHttpEngine {
  /**
   * @type HttpRequest
   */
  #engine = undefined;
  constructor() {
    this.#engine = new HttpRequest(`${CONFIG.serverAddr}/admin`);
    AdminHttpEngine.instance = this;
  }

  /**
   * @type { AdminHttpEngine }
   */
  static instance;
  static createInstance() {
    return new AdminHttpEngine();
  }

  extractDataFromToken(token) {
    const body = token.split('.')[1];
    const stringBody = Buffer.from(body, 'base64').toString();
    return JSON.parse(stringBody);
  }

  /**
   * @param { LoginBody } body
   * @return { Promise<{ statusCode: Number, body: LoginResponse}> }
   */
  async Login(body) {
    if (!(body instanceof LoginBody)) throw new Error('Wrong Typed');
    return await this.#engine.Post('/login', body);
  }
}
