import React from 'react';
import ReactDOM from 'react-dom';
import RouterModule from './app.router.module';
import 'bootstrap/dist/css/bootstrap.min.css';
import { CssBaseline, ThemeProvider } from '@mui/material';
// import ThemeProvider from '@mui/material/styles/ThemeProvider';
import StylesProvider from '@mui/material/StyledEngineProvider';
import theme from 'utils/colorPallette';
import './style.scss';

// import rtl from 'jss-rtl';
// import { create } from 'jss';
// import { jssPreset } from '@mui/styles';

import rtlPlugin from 'stylis-plugin-rtl';
import { CacheProvider } from '@emotion/react';
import createCache from '@emotion/cache';
import DBProvider from './db';
import { LocalizationProvider } from '@mui/x-date-pickers';
import jalaliAdapter from '@date-io/jalaali';

const cacheRtl = createCache({
  key: 'muirtl',
  stylisPlugins: [rtlPlugin]
});

// const rtlJss = create({
//   plugins: [...jssPreset().plugins, rtl()],
// })

ReactDOM.render(
  <React.StrictMode>
    <DBProvider>
      <CacheProvider value={cacheRtl}>
        <StylesProvider>
          <ThemeProvider theme={theme}>
            <LocalizationProvider dateAdapter={jalaliAdapter}>
              <CssBaseline />
              <RouterModule />
            </LocalizationProvider>
          </ThemeProvider>
        </StylesProvider>
      </CacheProvider>
    </DBProvider>
  </React.StrictMode>,
  document.getElementById('root')
);
