import { HttpRequest } from 'core/networking/http';
import config from 'assets/config/server.config';
import productsActions from 'db/products/actions';
import { AdminHttpEngine } from 'app/Login/login.service';

export class DashboardService {
  #http = new HttpRequest(config.CreatePath(''));
  #store = null;

  constructor() {
    this.#http.SetHeader('test', 'active');
  }

  SetStore(store) {
    if (this.#store) return;
    this.#store = store;
  }

  /**
   *
   * @return Promise<Object>
   */
  async GetShopData() {
    return await this.#http
      .Post('shop/get', { all: true })
      .then((response) => response.body)
      .catch((e) => {
        console.log(e);
      });
  }

  useLogin = () => {
    if (!AdminHttpEngine.instance) AdminHttpEngine.createInstance();
    return AdminHttpEngine.instance;
  };

  /**
   *
   * @param {Promise<Object>} task
   */
  async StoreShopDataAsync(task) {
    const response = await task;
    return this.StoreShopDataSync(response);
  }

  /**
   *
   * @param {any} data
   */
  StoreShopDataSync(data) {
    this.#store({
      type: productsActions.SET,
      payload: data
    });
    return data;
  }
}
export default DashboardService;
