import React from 'react';

const eachClass = `col-sm-6 mt-3 text-center`;

export function Grids(props) {
  const GetAll = (arr) => {
    return arr.map((item, index) => {
      if (index === 0 || index % 2 === 0) {
        return (
          <div className="row" key={`IND-${index}`}>
            <div className={eachClass}>{arr[index]}</div>
            {arr[index + 1] && <div className={eachClass}>{arr[index + 1]}</div>}
          </div>
        );
      }
      return <React.Fragment key={`NONE-${index}`}></React.Fragment>;
    });
  };
  return (
    <>
      {props.children instanceof Array ? (
        <div className="row">
          <div className="col-12 text-center m-auto">{GetAll(props.children)}</div>
        </div>
      ) : (
        <div className="row">
          <div className={eachClass}>{props.children}</div>
        </div>
      )}
    </>
  );
}
