import React from 'react';
import { motion } from 'framer-motion';

export function BounceIn(props) {
  return (
    <div className="row">
      <motion.div
        initial={{ opacity: 0, scaleX: '0', scaleY: '0' }}
        animate={{
          opacity: 1,
          scaleX: ['1.2', '0.8', '1'],
          scaleY: ['0.8', '1.2', '1']
        }}
        transition={{
          duration: props?.duration || 0.5,
          delay: props?.delay || 0
        }}
        className="col-12">
        {props.children}
      </motion.div>
    </div>
  );
}
