import React from 'react';
import { motion } from 'framer-motion';

export function ShowUpAnimated(props) {
  return (
    <div className="row">
      <motion.div
        initial={
          props.up || props.bottom
            ? { y: props.up ? 50 : -50, opacity: 0 }
            : { x: props.right ? 50 : -50, opacity: 0 }
        }
        animate={props.up || props.bottom ? { y: 0, opacity: 1 } : { x: 0, opacity: 1 }}
        transition={{
          duration: props?.duration || 0.25,
          delay: props?.delay || 0
        }}
        className="col-12">
        {props.children}
      </motion.div>
    </div>
  );
}
