export class RouteModel {
  constructor({ path, Component }) {
    if (!Component) throw new Error('Null Component');
    if (!path && path !== '') throw new Error('Null path');

    this.path = path;
    this.Component = Component;
  }

  path = '';
  Component = null;

  Parse = () => ({
    path: this.path,
    Component: this.Component
  });

  toString() {
    return this.Parse();
  }
}
