import { ShowUpAnimated } from 'components/animated/showUp';
import React from 'react';
import { HomeService } from './home.service';
import HomeView from './home.view';
import { connect } from 'react-redux';
import Loading from '@mui/material/CircularProgress';
import './home.style.scss';

class HomeModule extends React.Component {
  #service = new HomeService();

  constructor(props) {
    super(props);
    this.state = {
      loadingDashboard: true
    };

    this.StoreShopDataFromServer.bind(this);
    this.GetStatistics.bind(this);
  }

  componentDidMount() {
    this.#service.SetStore(this.props.dispatch);
    this.StoreShopDataFromServer();
    this.GetStatistics().then(this.OnStatisticsPrepared);
  }

  async GetStatistics() {
    const service = this.#service.getStatistics(this.props.token);
    const options = {
      users: !this.props.statistics.users,
      shop: !this.props.statistics.shop
    };
    const response = await this.#service.StoreStatisticsData(service, options);
    if (!options.users) response.users = this.props.statistics.users;
    if (!options.shop) response.shop = this.props.statistics.shop;

    const users = service.usersStatisticsService(response.users);
    const shop = service.shopStatisticsService(response.shop);
    const profit = shop.getFullSellAmount();
    const getSellPercentage = (all, v) => parseInt((v * 100) / all);
    const mostSelling = shop.getMostSelling().map((item) => ({
      name: item.date,
      sellAmount: getSellPercentage(profit, item['مقدار فروش'])
    }));
    return {
      usersCount: users.getUsersCount(),
      online: users.getOnlineCount(),
      profit,
      mostSelling
    };
  }

  /**
   *
   * @param {{
   *  usersCount: number,
   *  online: number,
   *  profit: string ,
   *  mostSelling: Array<{ name: string, sellAmount: number }>
   * }} data
   */
  OnStatisticsPrepared = (data) => {
    this.setState(
      {
        usersCount: data.usersCount,
        profitAmount: data.profit,
        onlineCount: data.online,
        mostSelling: data.mostSelling
      },
      () => {
        this.setState({
          loadingDashboard: false
        });
      }
    );
  };

  StoreShopDataFromServer() {
    this.#service.SotreShopDataAsync(this.#service.GetShopData());
  }

  render() {
    return (
      <>
        {this.state.loadingDashboard ? (
          <Loading />
        ) : (
          <ShowUpAnimated>
            <HomeView
              online={this.state.onlineCount}
              profit={this.state.profitAmount}
              usersCount={this.state.usersCount}
              mostSellingProducts={this.state.mostSelling}
            />
          </ShowUpAnimated>
        )}
      </>
    );
  }
}

const mapStateToProps = (state) => state;
export const HomeComponent = connect(mapStateToProps)(HomeModule);
export default HomeComponent;
