import { RouteModel } from 'utils/route.model';
import Router from './dashboard.router.component';
import Home from './Home';
import { NotFound } from '../NotFound';
import React, { lazy } from 'react';
import FilesModule from './Files';

const ShopModule = lazy(() => import('./Shop/index'));
const ShopLazy = (props) => (
  <React.Suspense fallback={<>Loading...</>}>
    {' '}
    <ShopModule {...props} />{' '}
  </React.Suspense>
);

const StatisticsModule = lazy(() => import('./Statistics/index'));
const StatisticsLazy = (props) => (
  <React.Suspense fallback={<>Loading...</>}>
    {' '}
    <StatisticsModule {...props} />{' '}
  </React.Suspense>
);

const SettingModule = lazy(() => import('./Setting/index'));
const SettingLazy = (props) => (
  <React.Suspense fallback={<>Loading...</>}>
    {' '}
    <SettingModule {...props} />{' '}
  </React.Suspense>
);

const AdminsModule = lazy(() => import('./Admin/index'));
const AdminsLazy = (props) => (
  <React.Suspense fallback={<>Loading...</>}>
    {' '}
    <AdminsModule {...props} />{' '}
  </React.Suspense>
);

const TasksModule = lazy(() => import('./Tasks/index'));
const TasksLazy = (props) => (
  <React.Suspense fallback={<>Loading...</>}>
    <TasksModule {...props} />
  </React.Suspense>
);

const ProfileModule = lazy(() => import('./Profile/index'));
const ProfileLazy = (props) => (
  <React.Suspense fallback={<>Loading...</>}>
    <ProfileModule {...props} />
  </React.Suspense>
);

export const RouterComponent = () => {
  return (
    <Router
      routerData={[
        new RouteModel({
          Component: (props) => {
            React.useEffect(() => {
              props.redirect('home');
            }, [0]);
            return <></>;
          },
          path: `/`
        }),
        new RouteModel({ Component: Home, path: `/home` }),
        new RouteModel({ Component: FilesModule, path: `/files/*` }),
        new RouteModel({ Component: ShopLazy, path: `/shop/*` }),
        new RouteModel({ Component: StatisticsLazy, path: `/statistics/*` }),
        new RouteModel({ Component: SettingLazy, path: `/setting` }),
        new RouteModel({ Component: AdminsLazy, path: `/admins/*` }),
        new RouteModel({ Component: TasksLazy, path: `/tasks/*` }),
        new RouteModel({ Component: ProfileLazy, path: `/profile/*` }),

        new RouteModel({ Component: NotFound, path: `*` })
      ]}
    />
  );
};

export default RouterComponent;
