import React from 'react';
import { Box, Typography, TextField, Button, InputAdornment, IconButton } from '@mui/material';
import PersonIcon from '@mui/icons-material/Person';
import EyeIcon from '@mui/icons-material/Visibility';
import EyeOffIcon from '@mui/icons-material/VisibilityOff';
import ColorPallete from 'utils/colorPallette';
import Circular from '@mui/material/CircularProgress';
import TOKEN_ACTIONS from 'db/token/action';
import { AdminHttpEngine } from './login.service';
import { connect } from 'react-redux';
import { LoginBody } from 'core/models/app/Login.models';
import SettingActions from 'db/setting/actions';
import USER_ACTIONS from 'db/user/action';

export class LoginRegisterPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      passwordType: 'password',
      isLoading: false,
      username: '',
      password: ''
    };
    const theme = localStorage.getItem('theme');
    if (theme === null) {
      localStorage.setItem('theme', 'light');
      this.props.dispatch({
        type: SettingActions.SET_DARK_MODE,
        payload: false
      });
    } else {
      this.props.dispatch({
        type: SettingActions.SET_DARK_MODE,
        payload: theme === 'dark'
      });
    }
  }

  #httpEngine = new AdminHttpEngine();

  loginRequest = (e) => {
    e.preventDefault();
    this.setState({ isLoading: true }, () => {
      this.#httpEngine
        .Login(new LoginBody(this.state.username, this.state.password))
        .then((response) => {
          const userInfo = this.#httpEngine.extractDataFromToken(response.body.Token);
          this.props.dispatch({
            type: USER_ACTIONS.SET,
            payload: {
              UserId: userInfo.UserId,
              UserName: userInfo.Username,
              Access: userInfo.Access
            }
          });
          this.props.dispatch({
            type: TOKEN_ACTIONS.SET,
            payload: response.body.Token
          });

          localStorage.setItem('token', response.body.Token);

          this.props.redirect('/dashboard');
        })
        .catch(console.trace)
        .finally(() => {
          this.setState({ isLoading: false });
        });
    });
  };

  render() {
    return (
      <div className="row">
        <div className="col-12 p-5">
          <div className="row">
            <div className="col-md-2"></div>
            <div className="col-lg-8">
              <Box
                className="p-3 rounded shadow-lg FadeDownAn"
                bgcolor={ColorPallete.palette.secondary.main}>
                <div className="row">
                  <div className="col-12">
                    <Typography
                      align="center"
                      color={ColorPallete.palette.text.secondary}
                      variant="h4"
                      className="p-3 textShadow-sm ">
                      ورود به داشبورد
                    </Typography>
                  </div>
                </div>

                <form action="#" onSubmit={this.loginRequest} autoComplete="off">
                  <div className="row my-5">
                    <div className="col-3"></div>
                    <div className="col-md-6 px-5 text-center ">
                      <TextField
                        label="نام کاربری"
                        variant="filled"
                        color="grey"
                        fullWidth
                        onChange={(e) => {
                          this.setState({
                            username: e.target.value
                          });
                        }}
                        className="shadow FadeRightAn"
                        InputProps={{
                          endAdornment: (
                            <InputAdornment position="end">
                              <PersonIcon color="info" />
                            </InputAdornment>
                          ),
                          style: {
                            color: ColorPallete.palette.text.secondary
                          }
                        }}
                      />
                    </div>
                  </div>

                  <div className="row my-5">
                    <div className="col-3"></div>
                    <div className="col-md-6 px-5 text-center ">
                      <TextField
                        label="رمز عبور"
                        variant="filled"
                        color="grey"
                        type={this.state.passwordType}
                        className="shadow FadeLeftAn"
                        fullWidth
                        onChange={(e) => {
                          this.setState({
                            password: e.target.value
                          });
                        }}
                        InputProps={{
                          endAdornment: (
                            <InputAdornment position="end">
                              <IconButton
                                onClick={() => {
                                  this.setState({
                                    passwordType:
                                      this.state.passwordType === 'password' ? 'text' : 'password'
                                  });
                                }}>
                                {this.state.passwordType === 'password' ? (
                                  <EyeIcon color="info" />
                                ) : (
                                  <EyeOffIcon color="info" />
                                )}
                              </IconButton>
                            </InputAdornment>
                          ),
                          autoComplete: 'on',
                          style: {
                            color: ColorPallete.palette.text.secondary
                          }
                        }}
                      />
                    </div>
                  </div>

                  <div className="row pb-5 pt-3">
                    <div className="col-12 text-center">
                      <Button
                        color="info"
                        variant="outlined"
                        className="w-25 px-5 shadow-lg p-2 FadeUpAn"
                        type="submit">
                        {this.state.isLoading ? (
                          <Circular color="info" size={'1.5rem'} className="FadeUpAn" />
                        ) : (
                          'ورود'
                        )}
                      </Button>
                    </div>
                  </div>
                </form>
              </Box>
            </div>
            <div className="col-md-2"></div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => state;
export default connect(mapStateToProps)(LoginRegisterPage);
