import React from 'react';
import TextField from '@mui/material/TextField';
import Checkbox from '@mui/material/Checkbox';
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel';
import FormControlLabel from '@mui/material/FormControlLabel';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import { ShowUpAnimated } from 'components/animated/showUp';
import ObjectIcon from '@mui/icons-material/ViewInArTwoTone';
import { Button, Typography } from '@mui/material';
import { useEffect } from 'react';
import Progress from '@mui/material/CircularProgress';

function Row(props) {
  return (
    <div className="row">
      <div className={`col-12 p-3 ${props.className} col-12`}>{props.children}</div>
    </div>
  );
}

export function CreateFile(props) {
  const [compeleted, setCompeleted] = React.useState(false);
  const [locked, setLocked] = React.useState(true);
  const [formData, setFormData] = React.useState({
    Name: '',
    Platform: 11,
    Type: 2,
    Version: '1.0.0',
    Required: true,
    ProductId: props.products[0]._id
  });

  const [file, setFile] = React.useState({
    name: '',
    size: 0
  });

  useEffect(() => {
    console.log('Changed...');
  }, [formData.Required]);

  /**
   *
   * @param {String} version
   */
  const VersionCheck = (version) => {
    const all = version.split('.');
    if (all.length !== 3) return false;

    const major = parseInt(all[0]);
    const minior = parseInt(all[1]);
    const patch = parseInt(all[2]);
    console.log(isNaN(major), isNaN(minior), isNaN(patch));
    console.log(all);
    if (isNaN(major) || isNaN(minior) || isNaN(patch)) return false;
    return true;
  };

  const CheckForCompelete = () => {
    console.log(formData);
    if (!formData.Name || !formData.Version || !file.size) {
      if (compeleted) setCompeleted(false);
      return;
    }

    const v = VersionCheck(formData.Version);
    console.log(v);
    if (!v) {
      setCompeleted(false);
      return;
    }

    setCompeleted(true);
  };

  const OnBlur = (name) => (e) => {
    setFormData({
      ...formData,
      [name]: e.target.value
    });
    CheckForCompelete();
  };

  const OnChange = (name) => (e, v) => {
    setFormData({
      ...formData,
      [name]: v
    });
    CheckForCompelete();
  };

  const OnSubmit = () => {
    props.onSubmit(file, formData).then(() => {
      // setFormData({
      //   Name: '',
      //   Platform: 11,
      //   Type: 2,
      //   Version: '1.0.0',
      //   Required: true,
      //   ProductId: props.products[0]._id
      // });
      setFormData({
        ...formData,
        Name: ''
      });
      setFile({
        name: '',
        size: 0
      });
    });
  };

  const OnFileUpload = (e) => {
    if (!e) return;
    const targetFile = e.target.files[0];
    if (!targetFile) return;
    setFile(targetFile);
    if (!formData.Name) {
      setFormData({
        ...formData,
        Name: targetFile.name
      });
    }
  };

  const GetSize = (size) => {
    const targetSizeInKb = parseInt((size / 1024) * 1000) / 1000;
    const targetSizeInMb = parseInt((targetSizeInKb / 1024) * 1000) / 1000;

    return (
      <div className="row">
        <div className="col px-1 text-start">{targetSizeInMb < 0.1 ? 'Kb' : 'Mb'}</div>
        <div className="col px-0 text-end">
          {targetSizeInMb < 0.1 ? targetSizeInKb : targetSizeInMb}
        </div>
      </div>
    );
  };

  useEffect(CheckForCompelete, [formData, compeleted, file.size]);

  const getPercent = (l, t) => {
    return (l * 100) / t;
  };

  return (
    <ShowUpAnimated>
      <Row className="p-5 py-3 text-center">
        {props.progress.total === -1 ? (
          <ObjectIcon
            fontSize="large"
            color={compeleted ? 'success' : 'disabled'}
            sx={{ fontSize: compeleted ? '4rem' : '3rem', transition: '0.25s ease' }}
          />
        ) : (
          <Progress
            variant="determinate"
            value={getPercent(props.progress.loaded, props.progress.total)}
          />
        )}
      </Row>

      <Row className="text-center">
        <div className="col-12 col-sm-10 col-md-6 mx-auto">
          <Button className="p-0 w-100 shadow text-end" variant="outlined" color="primary">
            <label htmlFor="file_inp" className="h-100 w-100 px-4 py-2 cursor-pointer	">
              <div className="row">
                <div className="col-6">
                  {!file.name ? (
                    'آپلود فایل'
                  ) : (
                    <Typography noWrap align="right" variant="caption">
                      {' '}
                      <ObjectIcon className="ms-2" fontSize="small" /> {file.name}{' '}
                    </Typography>
                  )}
                </div>
                {!file.size ? <></> : <div className="col text-start">{GetSize(file.size)}</div>}
              </div>
            </label>
          </Button>
          <input id="file_inp" type={'file'} onChange={OnFileUpload} />
        </div>
      </Row>

      <Row className="text-center">
        <TextField
          variant="outlined"
          value={formData.Name}
          className="shadow col-12 col-sm-10 col-md-6"
          label="اسم فایل"
          onChange={OnBlur('Name')}
          onBlur={OnBlur('Name')}
          color="primary"
        />
      </Row>
      <Row className="text-center">
        <FormControl className="col-12 col-sm-10 col-md-6">
          <InputLabel id="plt-select">پلتفرم انتشار</InputLabel>
          <Select
            labelId="plt-select"
            defaultValue={formData.Platform}
            variant="outlined"
            className="shadow  text-end"
            label="پلتفرم انتشار"
            onChange={OnBlur('Platform')}
            color="primary">
            <MenuItem value={11}>Android</MenuItem>
            <MenuItem value={8}>IOS</MenuItem>
            <MenuItem value={7}>Unity Editor</MenuItem>
          </Select>
        </FormControl>
      </Row>
      <Row className="text-center">
        <FormControl className="col-12 col-sm-10 col-md-6">
          <InputLabel id="type-select">نوع فایل</InputLabel>
          <Select
            labelId="type-select"
            defaultValue={formData.Type}
            variant="outlined"
            className="shadow  text-end"
            label="نوع فایل"
            onChange={(e) => {
              OnBlur('Type')(e);
              if (e.target.value < 3) {
                OnChange('Required')(null, true);
                setLocked(true);
              } else {
                setLocked(false);
              }
            }}
            color="primary">
            <MenuItem value={1}>آواتار</MenuItem>
            <MenuItem value={2}>پیش نمایش</MenuItem>
            <MenuItem value={3}>مدل 3 بعدی</MenuItem>
          </Select>
        </FormControl>
      </Row>
      <Row className="text-center">
        <FormControl className="col-12 col-sm-10 col-md-6">
          <InputLabel id="plt-select">برای محصول</InputLabel>
          <Select
            labelId="plt-select"
            defaultValue={formData.ProductId}
            variant="outlined"
            className="shadow  text-end"
            label="برای محصول"
            onChange={OnBlur('ProductId')}
            color="primary">
            {props.products.map((product) => (
              <MenuItem key={`pr-list-${product._id}`} value={product._id}>
                {product.Name}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      </Row>
      <Row className="text-center">
        <TextField
          defaultValue={formData.Version}
          variant="outlined"
          className="shadow col-12 col-sm-10 col-md-6"
          label="ورژن فایل"
          onChange={OnBlur('Version')}
          onBlur={OnBlur('Version')}
          color="primary"
        />
      </Row>
      <Row className="text-center">
        <FormControl className="col-12 col-sm-10 col-md-6 ">
          <FormControlLabel
            disabled={locked}
            control={
              <Checkbox
                onChange={(e, v) => {
                  OnChange('Required')(null, v);
                }}
                checked={formData.Required}
                variant="outlined"
                color="primary"
              />
            }
            label="ضروری برای دانلود"
          />
        </FormControl>
      </Row>
      <Row>
        <Button onClick={OnSubmit}>ثبت اطلاعات</Button>
      </Row>
    </ShowUpAnimated>
  );
}

export default CreateFile;
