import React from 'react';
import { BrowserRouter, Route, Routes, useMatch, useNavigate, useLocation } from 'react-router-dom';

function RouterWrapper({ Component, path, parent }) {
  const match = useMatch(path);
  const navigate = useNavigate();
  const loc = useLocation();

  const ChangeRoute = (p) => {
    parent.current.classList.remove('shown');
    parent.current.classList.add('hide');
    setTimeout(() => {
      navigate(p);
      parent.current.classList.remove('hide');
      parent.current.classList.add('shown');
    }, 250);
  };

  return <Component match={match} redirect={ChangeRoute} locatio={loc} />;
}

export class AppRouter extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.parent = React.createRef();
  }

  parent = undefined;

  render() {
    return (
      <div className="container-fluid" ref={this.parent}>
        <BrowserRouter>
          <Routes>
            {!this.props.routerData ? (
              <></>
            ) : (
              this.props.routerData.map(({ path, Component }, index) => (
                <Route
                  key={`Router-${index}`}
                  path={path}
                  element={<RouterWrapper parent={this.parent} Component={Component} path={path} />}
                />
              ))
            )}
          </Routes>
        </BrowserRouter>
      </div>
    );
  }
}

export default AppRouter;
