import axios from 'axios';
import NotAuthorizedException from 'core/errors/NotAuthorizedException';
import { HttpBody } from 'core/models/networking/bodyType.model';
import { HttpResponseModel } from 'core/models/networking/httpResponse.model';

export class HttpRequest {
  #path = '';
  #headers = {};

  /**
   * @type {Array<HttpRequest>}
   */
  static httpInstances = [];
  static ClearAllAuthorizations = () => {
    for (const item of HttpRequest.httpInstances) {
      item.UnsetHeader('authorization');
    }
  };

  constructor(path) {
    this.#path = path;
    HttpRequest.httpInstances.push(this);
  }

  SetHeader(key, value) {
    this.#headers[key] = value;
    return this;
  }
  UnsetHeader(key) {
    this.#headers[key] = '';
    delete this.#headers[key];
    return this;
  }

  #fileUploadStream = () => false;

  subscribeToFileUploadStream(action) {
    this.#fileUploadStream = action;
  }

  changePath(path) {
    const http = new HttpRequest(path);
    for (const key in this.#headers) {
      http.SetHeader(key, this.#headers[key]);
    }
    return http;
  }

  async Post(subPath, body) {
    // if (subPath[0] === "/") subPath[0] = "";
    subPath = subPath.replace(' ', '_');
    try {
      const response = await axios.post(
        `${this.#path}${subPath}`,
        body instanceof HttpBody ? body.toJson() : body,
        {
          headers: this.#headers,
          onUploadProgress: this.#fileUploadStream
        }
      );
      return new HttpResponseModel({
        code: response.status,
        data: response.data
      });
    } catch (e) {
      if (e.response) {
        if (e.response.status === 401) {
          throw new NotAuthorizedException('');
        }
      }
      throw e;
    }
  }

  async Get(subPath) {
    subPath = subPath.replace(' ', '_');
    try {
      const response = await axios.get(`${this.#path}${subPath}`, {
        headers: this.#headers
      });
      return new HttpResponseModel({
        code: response.status,
        data: response.data
      });
    } catch (e) {
      if (e.response) {
        if (e.response.status === 401) {
          throw new NotAuthorizedException('');
        }
      }
      throw e;
    }
  }

  async Put(subPath, body) {
    subPath = subPath.replace(' ', '_');
    try {
      const response = await axios.put(
        `${this.#path}${subPath}`,
        body instanceof HttpBody ? body.toJson() : body,
        {
          headers: this.#headers,
          onUploadProgress: this.#fileUploadStream
        }
      );
      return new HttpResponseModel({
        code: response.status,
        data: response.data
      });
    } catch (e) {
      if (e.response) {
        if (e.response.status === 401) {
          throw new NotAuthorizedException('');
        }
      }
      throw e;
    }
  }

  async Delete(subPath, body) {
    subPath = subPath.replace(' ', '_');
    try {
      console.log(this.#headers);
      const response = await axios.delete(
        `${this.#path}${subPath}`,
        body instanceof HttpBody ? body.toJson() : body,
        {
          headers: this.#headers
        }
      );
      return new HttpResponseModel({
        code: response.status,
        data: response.data
      });
    } catch (e) {
      if (e.response) {
        if (e.response.status === 401) {
          throw new NotAuthorizedException('');
        }
      }
      throw e;
    }
  }
}

export default HttpRequest;
