import CONFIG from 'assets/config/server.config';
import HttpRequest from 'core/networking/http';

export class FilesHttpModule {
  /**
   * @type HttpRequest
   */
  #engine = undefined;
  constructor(token) {
    this.#engine = new HttpRequest(CONFIG.CreatePath('files'));
    this.#engine.SetHeader('Authorization', token);
    this.#engine.SetHeader('test', 'active');
  }

  async GetAll(body) {
    return await this.#engine.Post('/get', body);
  }

  async CreateFile(file, formData, uploadStream) {
    const form = new FormData();
    form.append('payload', file, file.name);
    this.#engine.subscribeToFileUploadStream((response) => {
      uploadStream(response.loaded, response.total);
    });

    for (const item in formData) {
      form.append(item, formData[item]);
    }

    return await this.#engine.Post('/create', form);
  }

  async DeletFile(id) {
    try {
      const admin = this.#engine.changePath(CONFIG.CreatePath('admin'));
      const response = await admin.Delete('/files/' + id, {});
      return response.body;
    } catch (e) {
      console.log(e);
      return 0;
    }
  }
}
