import React from 'react';
import { RouteModel } from './utils/route.model';
import Router from './app.router.component';
import LoginRegisterPage from './app/Login';
import { NotFound } from './app/NotFound';
import DashBoard from './app/dashboard';

export const RouterComponent = () => {
  return (
    <Router
      routerData={[
        new RouteModel({ Component: LoginRegisterPage, path: '/' }),
        new RouteModel({ Component: DashBoard, path: '/dashboard/*' }),
        new RouteModel({ Component: NotFound, path: '*' })
      ]}
    />
  );
};

export default RouterComponent;
