import React from 'react';
import { Button, Typography } from '@mui/material';
import { Grids } from 'components/GridButtons';
import { FileItems } from 'components/FileItems';
import AddRoundedIcon from '@mui/icons-material/AddRounded';
import LoadingSection from 'components/Loading/section';

export function FilesView(props) {
  return (
    <div className="row">
      <div className="col-12 p-5 pt-0">
        <div className="row">
          <div className="col-12 py-3 border-bottom mb-4">
            <Typography variant="h5">مدیریت فایل ها</Typography>
          </div>
        </div>
        <div className="row">
          <div className="col-12">
            <Button variant="contained" onClick={props.swtichToCreate}>
              <AddRoundedIcon
                className="rounded-circle shadow ms-3"
                sx={{ bgcolor: 'rgba(255,255,255,0.2)' }}
              />
              اضافه کردن
            </Button>
          </div>
        </div>
        <div className="row">
          <div className="col-12">
            {props.payload === null ? (
              <div className="row">
                <div className="col-12 px-5 py-3 text-center">
                  <div className="m-auto">
                    <LoadingSection />
                  </div>
                </div>
              </div>
            ) : !props.payload && props.payload !== [] ? (
              <div className="text-center text-danger p-3 font-weight-bold">
                خطا در ارتباط با سرور
              </div>
            ) : (
              <Grids>
                {props.payload.map((item, index) => (
                  <FileItems
                    key={`FI-${index}`}
                    OnSelect={(id) => {
                      if (!props.onClick) return;
                      props.onClick(id, item);
                    }}
                    Id={item._id}>
                    {item.Name}
                  </FileItems>
                ))}
              </Grids>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default FilesView;
