import React from 'react';
import Typo from '@mui/material/Typography';
import ObjectIcon from '@mui/icons-material/ViewInArTwoTone';
import { BounceIn } from 'components/animated/bounceIn';
import { ShowUpAnimated } from 'components/animated/showUp';
import { Button, IconButton, Tooltip } from '@mui/material';
import UpgradeIcon from '@mui/icons-material/UpgradeRounded';
import DeleteIcon from '@mui/icons-material/DeleteRounded';
import DownloadButton from '@mui/icons-material/DownloadRounded';
import CONFIG from 'assets/config/server.config';
import { CloseRounded } from '@material-ui/icons';

const typesName = ['آواتار', 'پیش نمایش', 'آبجکت 3 بعدی'];
const platforms = {
  7: 'Unity Editor',
  11: 'Android',
  8: 'IOS'
};

export function SingleFileView({ fileInfo, onDelete }) {
  const [version, setVersion] = React.useState(fileInfo.Version);
  const [file, setFile] = React.useState({
    name: '',
    size: 0
  });

  const OnFileUpload = (e) => {
    if (!e) return;
    const targetFile = e.target.files[0];
    if (!targetFile) return;
    setFile(targetFile);
    console.log(version, fileInfo);
    if (version === fileInfo.Version) {
      const sp = version.split('.');
      sp[1] = parseInt(sp[1]) + 1;
      setVersion(sp.join('.'));
    }
  };

  const GetSize = (size) => {
    const targetSizeInKb = parseInt((size / 1024) * 1000) / 1000;
    const targetSizeInMb = parseInt((targetSizeInKb / 1024) * 1000) / 1000;

    return (
      <div className="row">
        <div className="col px-1 text-start">{targetSizeInMb < 0.1 ? 'Kb' : 'Mb'}</div>
        <div className="col px-0 text-end">
          {targetSizeInMb < 0.1 ? targetSizeInKb : targetSizeInMb}
        </div>
      </div>
    );
  };

  const CancelFile = () => {
    setFile({
      name: '',
      size: 0
    });
    setVersion(fileInfo.version);
  };

  const DeleteFile = () => {
    onDelete(fileInfo._id).then((d) => {
      console.log(d);
      alert('پاک شد');
    });
  };
  const UpdateFile = () => {
    alert('Not Implemented ');
  };

  return (
    <div className="row">
      <div className="col-12 position-relative">
        {!fileInfo ? (
          <>Loading Data ... </>
        ) : (
          <>
            <div className="row">
              <div className="col-12 text-center p-4">
                <BounceIn delay={0.5}>
                  <ObjectIcon
                    color="primary"
                    fontSize="large"
                    sx={{
                      bgcolor: 'var(--color-light-primary)', // "rgba(179, 174, 245, 0.3)",
                      fontSize: '4rem',
                      borderRadius: '10px',
                      boxShadow:
                        'inset 0px 0px 20px rgba(60, 50, 191, 0.4), 3px 4px 15px rgba(0, 0, 0, 0.3)'
                    }}
                    className="mx-auto"
                  />
                </BounceIn>
              </div>
            </div>
            <div className="row">
              <div className="col-12">
                <ShowUpAnimated delay={0.75}>
                  <div className="row">
                    <div className="col-12">
                      <Typo variant="h5" fontWeight={'bold'} align="center">
                        {fileInfo.Name}
                      </Typo>
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-12 p-3 text-center">
                      نوع فایل : {typesName[fileInfo.Type - 1]}
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-12 p-3 text-center">
                      پلتفرم : {platforms[fileInfo.Platforms]}
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-12 py-2 text-center">
                      ورژن : {version}
                      {/* <TextField value={version} label='ورژن' className='w-50 shadow' /> */}
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-12">
                      <div className="col-12 col-sm-10 col-md-6 mx-auto">
                        <Button className="p-0 w-100 text-end" variant="outlined" color="primary">
                          <label
                            htmlFor="file_inp"
                            className="h-100 w-100 px-4 py-2 cursor-pointer	">
                            <div className="row">
                              <div className="col-6">
                                {!file.name ? (
                                  'آپلود ورژن جدید'
                                ) : (
                                  <Typo noWrap align="right" variant="caption">
                                    {' '}
                                    <ObjectIcon className="ms-2" fontSize="small" /> {file.name}{' '}
                                  </Typo>
                                )}
                              </div>
                              {!file.size ? (
                                <></>
                              ) : (
                                <div className="col text-start">{GetSize(file.size)}</div>
                              )}
                            </div>
                          </label>
                        </Button>
                        <input id="file_inp" type={'file'} onChange={OnFileUpload} />
                      </div>
                    </div>
                  </div>

                  {file.size > 0 && (
                    <div className="row">
                      <div className="col-12 pt-4 text-center">
                        <Button
                          className="w-25 shadow mx-2"
                          color="secondary"
                          onClick={UpdateFile}
                          variant="contained">
                          آپدیت فایل <UpgradeIcon />
                        </Button>
                        <Button
                          className="w-25 shadow mx-2"
                          color="primary"
                          onClick={CancelFile}
                          variant="contained">
                          انصراف <CloseRounded />
                        </Button>
                      </div>
                    </div>
                  )}

                  <div className="row position-absolute top-0 end-0 px-3 w-25">
                    <div className="col-12">
                      <div className="row">
                        <div className="col-3 text-center">
                          <Tooltip title="حذف فایل" placement="left">
                            <IconButton className="shadow" color="error" onClick={DeleteFile}>
                              <DeleteIcon />
                            </IconButton>
                          </Tooltip>
                        </div>
                        <div className="col-3 text-center">
                          <Tooltip title="دانلود فایل" placement="left">
                            <IconButton
                              className="shadow"
                              color="primary"
                              href={CONFIG.GetFilePath(fileInfo._id)}>
                              <DownloadButton />
                            </IconButton>
                          </Tooltip>
                        </div>
                      </div>
                    </div>
                  </div>
                </ShowUpAnimated>
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
}

export default SingleFileView;
