import React from 'react';
import './NavigationStyles.scss';
import Box from '@mui/material/Box';
import ColorPallet from 'utils/colorPallette';
import Typo from '@mui/material/Typography';
import Button from '@mui/material/Button';
import MenuIcon from '@mui/icons-material/ArrowLeftRounded';
import IconButton from '@mui/material/IconButton';
import { useLocation, useNavigate } from 'react-router-dom';
import Logo from 'assets/GameLogo.png';

export function SidenavItems({ Name, Icon, to, Disabled }) {
  const [color, setColor] = React.useState(false);
  const [routeChange, setRouteChange] = React.useState(1);
  const loc = useLocation();
  const nav = useNavigate();
  React.useEffect(() => {
    if (loc.pathname.includes(`/dashboard/${to}`)) {
      setColor(true);
    } else {
      setColor(false);
    }
  }, [loc.pathname]);
  // color={color ? "secondary" : "primary"}

  return (
    <div className="row mb-3">
      <div className="col-12 text-center px-0">
        <Button
          variant={'text'}
          onClick={() => {
            setRouteChange(routeChange + 1);
            nav(to);
          }}
          dir="rtl"
          disabled={Disabled}
          className={`row p-2 rounded-0 ${
            color && !Disabled ? 'shadow activeBtn' : 'shadow-none inactiveBtn'
          }`}
          fullWidth>
          <div className="col-1 text-center">
            <Icon fontSize="small" className="ml-3 float-left" />
          </div>
          <div className="col-9 text-right">
            <Typo className="px-2 textShadow" align="right">
              {Name}
            </Typo>
          </div>
        </Button>
      </div>
    </div>
  );
}

export function SideNav(props) {
  const [showState, setShow] = React.useState(false);
  return (
    <>
      <div className={`buttonHolder ${showState ? 'close' : ''}`}>
        <IconButton
          color="primary"
          className={`shadow btn`}
          onClick={() => {
            setShow(!showState);
          }}>
          <MenuIcon color="info" className="icn" />
        </IconButton>
      </div>
      <div className={`row sidenav ${showState ? 'shown' : ''} m-0 p-0`}>
        <Box className="col-12 h-100 bx shadow-lg" bgcolor={ColorPallet.palette.grey.main}>
          <div className="row">
            <div className="col-12 text-center p-4 pb-2">
              <img src={Logo} alt="s" width={75} height={75} />
              {/* <PersonIcon color='grey' fontSize='large' /> */}
            </div>
          </div>
          <div className="row mb-5">
            <div className="col-12">
              <Typo color={'secondary'} align="center" className="pb-3 textShadow">
                {/* {props.username ?? "ادمین"} */}
                پنل ادمین مافیا نایتز
              </Typo>
            </div>
          </div>

          {props.children}
        </Box>
      </div>
    </>
  );
}

export default SideNav;
