import React from 'react';
import Box from '@material-ui/core/Box';
import { motion } from 'framer-motion';
import './style.scss';

const animationKeys = {
  init: { x: 50, opacity: 0 },
  animate: { x: 0, opacity: 1 }
};

export function TripleBox(props) {
  return (
    <div className={`row mt-3 ${props.className && props.className}`}>
      {!(props.children instanceof Array) ? (
        <motion.div
          initial={animationKeys.init}
          animate={animationKeys.animate}
          transition={{ duration: 0.215, delay: 0.25 }}
          className="col-md my-2 mt-sm-0">
          <Box className="p-3 rounded shadow boxOne">{props.children}</Box>
        </motion.div>
      ) : (
        <>
          {props.children[0] && (
            <motion.div
              initial={animationKeys.init}
              animate={animationKeys.animate}
              transition={{ duration: 0.215, delay: 0.25 }}
              className="col-md my-2 mt-sm-0">
              <Box className="p-3 rounded shadow boxOne">{props.children[0]}</Box>
            </motion.div>
          )}
          {props.children[1] && (
            <motion.div
              initial={animationKeys.init}
              animate={animationKeys.animate}
              transition={{ duration: 0.25, delay: 0.5 }}
              className="col-md my-2 mt-sm-0">
              <Box className="p-3 rounded shadow boxTwo">{props.children[1]}</Box>
            </motion.div>
          )}
          {props.children[2] && (
            <motion.div
              initial={animationKeys.init}
              animate={animationKeys.animate}
              transition={{ duration: 0.25, delay: 0.75 }}
              className="col-md my-2 mt-sm-0">
              <Box className="p-3 rounded shadow boxThree">{props.children[2]}</Box>
            </motion.div>
          )}
        </>
      )}
    </div>
  );
}

export default TripleBox;
