import HttpRequest from 'core/networking/http';
import CONFIG from 'assets/config/server.config';

export class TasksService {
  #engine = new HttpRequest(CONFIG.CreatePath('admin'));
  constructor(token) {
    this.#engine.SetHeader('authorization', token);
    TasksService.instance = this;
  }

  /**
   * @type { TasksService }
   */
  static instance;

  async getAll() {
    try {
      const data = await this.#engine.Get('/tasks');
      console.log(data);
      return {
        status: 1,
        data: data.body
      };
    } catch (e) {
      console.trace(e);
      return {
        status: -1,
        data: e.response
      };
    }
  }

  async deletTask(id) {
    try {
      const data = await this.#engine.Delete('/tasks/' + id);
      return {
        status: 1,
        data: data.body
      };
    } catch (e) {
      console.trace(e);
      console.log(Object(e));
      return {
        status: -1,
        data: e.response
      };
    }
  }

  async updateTask(id, status) {
    try {
      const data = await this.#engine.Put('/tasks/' + id, {
        Status: status
      });
      return {
        status: 1,
        data: data.body
      };
    } catch (e) {
      console.trace(e);
      console.log(Object(e));
      return {
        status: -1,
        data: e.response
      };
    }
  }
}
