import { StatisticsActions } from 'db/statistics/actions';
import { HttpRequest } from 'core/networking/http';
import CONFIG from 'assets/config/server.config';
import productsActions from 'db/products/actions';
import StatisticsService from '../Statistics/statistics.service';

export class HomeService {
  #engine = new HttpRequest(CONFIG.CreatePath(''));
  #store = null;
  constructor() {
    this.#engine.SetHeader('test', 'active');
  }

  SetStore(store) {
    if (this.#store) return;
    this.#store = store;
  }

  getStatistics(token) {
    if (!StatisticsService.instance) {
      if (!token) return null;
      return new StatisticsService(token);
    }
    return StatisticsService.instance;
  }

  /**
   *
   * @param {StatisticsService} statisticsService
   * @returns
   */
  async StoreStatisticsData(statisticsService, options = { users: true, shop: true }) {
    const result = {};
    if (options.shop) {
      const response = await statisticsService.GetShopStatistics();
      if (response !== 0) {
        this.#store({
          type: StatisticsActions.SET_SHOP,
          payload: response
        });
        result['shop'] = response;
      }
    }
    if (options.users) {
      const response = await statisticsService.GetUsersStatistics();
      if (response !== 0) {
        this.#store({
          type: StatisticsActions.SET_USERS,
          payload: response
        });
        result['users'] = response;
      }
    }
    return result;
  }

  /**
   *
   * @return Promise<Object>
   */
  async GetShopData() {
    return await this.#engine
      .Post('shop/get', { all: true })
      .then((response) => response.body)
      .catch((e) => {
        console.log(e);
      });
  }

  /**
   *
   * @param {Promise<Object>} task
   */
  async SotreShopDataAsync(task) {
    const response = await task;
    return this.SotreShopDataSync(response);
  }

  /**
   *
   * @param {any} data
   */
  SotreShopDataSync(data) {
    this.#store({
      type: productsActions.SET,
      payload: data
    });
    return data;
  }
}
