import React from 'react';
import { ListItemIcon, ListItemText, MenuItem, Typography } from '@mui/material';
import NotSeenIcon from '@mui/icons-material/EmailRounded';
import SeenIcon from '@mui/icons-material/EmailOutlined';

function Messages(props) {
  const messages = props.messages.reverse();
  const [notSeen, setNsm] = React.useState([]);

  const onMessageSet = () => {
    if (messages.length > 0) {
      const ls = JSON.parse(localStorage.getItem(props.id + '@seenMessages')) || [];
      const nsm = messages
        .filter((item) => {
          return !ls.includes(item._id);
        })
        .map((item) => item._id);
      setNsm(nsm);
      const all = messages.map((i) => i._id);
      localStorage.setItem(props.id + '@seenMessages', JSON.stringify(all));
    }
  };
  React.useEffect(onMessageSet, [messages]);
  return (
    <>
      {!messages.length > 0 && (
        <div className="row">
          <div className="col-12">
            <Typography className="p-2">پیامی وجود ندارد</Typography>
          </div>
        </div>
      )}
      {messages.length > 0 &&
        messages.map((item, index) => (
          <MenuItem
            key={`MIT-${index}`}
            sx={{ width: '50vw' }}
            onClick={() => props?.onSelect(item._id)}>
            <ListItemIcon>
              {!notSeen.includes(item._id) ? <SeenIcon /> : <NotSeenIcon />}
            </ListItemIcon>
            <ListItemText>
              <div className="row">
                <div className="col-md-2">
                  <Typography variant="body2">{item.Creator.Username}</Typography>
                </div>
                <div className="col-md">
                  <Typography variant="body2" noWrap>
                    {' '}
                    {item.Title}
                  </Typography>
                </div>
              </div>
            </ListItemText>
          </MenuItem>
        ))}
    </>
  );
}

export const MessagesWidget = Messages;
export default MessagesWidget;
