import React from 'react';
import { Component } from 'react';
import { connect } from 'react-redux';
import { AuthGuard } from './auth.guard';
import TOKEN_ACTION from 'db/token/action';
import NotAuthorizedException from 'core/errors/NotAuthorizedException';
import axios from 'axios';

const errors = Object.freeze({
  http: 'http',
  noToken: 'no_token',
  none: '_'
});

class AuthGuardModule extends Component {
  #guard = undefined;
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      authorized: false,
      validationInProgress: false,
      error: errors.none
    };
  }

  componentDidUpdate() {
    if (!this.state.authorized && this.props.auth !== 'none' && !this.state.validationInProgress) {
      console.log('mount');
      this.onMount();
    }
  }

  setGuard = (auth = '', attempt = 0) => {
    if (attempt === 3) return false;
    try {
      this.#guard = new AuthGuard(auth || this.props.auth);
      return true;
    } catch (e) {
      if (e instanceof NotAuthorizedException) {
        const token = localStorage.getItem('token');

        const dispatchResult = this.props.dispatch({
          type: TOKEN_ACTION.SET,
          payload: token
        });
        return this.setGuard(dispatchResult.payload, attempt + 1);
      } else {
        this.props.authorizationFailed();
        return false;
      }
    }
  };

  validateGuard(onEnd = () => undefined) {
    if (this.state.error === errors.http) {
      return;
    }
    this.#guard
      .ValidateToken()
      .then(() => {
        this.setState({
          authorized: true,
          error: errors.none
        });
        axios.defaults.headers.common['authorization'] = this.props.auth;
      })
      .catch(() => {
        this.setState(
          {
            authorized: false,
            error: errors.http
          },
          () => {
            console.log('at http');
            this.props.authorizationFailed();
          }
        );
      })
      .finally(() => {
        this.setState(
          {
            loading: false
          },
          onEnd
        );
      });
  }

  onMount = () => {
    this.setState({
      validationInProgress: true
    });
    if (this.setGuard()) {
      this.validateGuard(() => {
        this.setState({
          validationInProgress: false
        });
      });
    } else {
      this.setState(
        {
          authorized: false,
          loading: false,
          validationInProgress: false,
          error: errors.noToken
        },
        () => {
          this.props.authorizationFailed();
          console.log('guard problem');
        }
      );
    }
  };

  componentDidMount() {
    this.onMount();
  }

  // componentDidCatch() {
  //   this.props.authorizationFailed();
  // }

  componentWillUnmount() {
    axios.defaults.headers.common['authorization'] = '';
    delete axios.defaults.headers.common['authorization'];
  }
  render() {
    return (
      <>
        {this.state.loading ? (
          <>Authorizing...</>
        ) : (
          <>{this.state.authorized ? this.props.children : <> Authorized Failed !</>}</>
        )}
      </>
    );
  }
}

const mapStateToProps = (state) => ({ auth: state.token });
const AuthGuardProvider = connect(mapStateToProps)(AuthGuardModule);
export default AuthGuardProvider;
