import React from 'react';
import FilesView from './files.view';
import { ShowUpAnimated } from 'components/animated/showUp';
import { FilesHttpModule } from './files.service';
import { connect } from 'react-redux';
import SingleFileView from './single_file.view';
import CreateFileView from './create_file.view';
import { Routes, Route, useNavigate, Navigate } from 'react-router-dom';
import { NotFound } from 'app/NotFound';
import './files.style.scss';

export class FilesModule extends React.Component {
  http = undefined;
  viewsId = {
    singleFileView: 'single_file',
    filesView: 'files_list',
    createFile: 'create_file'
  };

  constructor(props) {
    super();
    this.state = {
      viewId: '',
      filesState: {
        filesPayload: null
      },
      singleFileData: {
        fileData: null
      },
      uploadFile: {
        loaded: 0,
        total: -1
      }
    };

    this.http = new FilesHttpModule(props.token);
  }

  componentDidMount() {
    const state = {
      viewId: this.viewsId.filesView
    };
    this.setState(state, this.FetchData);
  }

  FetchData = () => {
    this.http.GetAll().then(this.SetData).catch(this.SetData);
  };

  SetData = (response) => {
    console.log(response);
    if (response instanceof String && response.startsWith('@ERROR')) {
      this.setState({
        filesState: {
          filesPayload: false
        }
      });
      return;
    }
    this.setState({
      filesState: {
        filesPayload: response.body
      }
    });
  };

  onClick = (_id, data) => {
    this.setState(
      {
        singleFileData: {
          ...this.state.singleFileData,
          fileData: data
        }
      },
      () => {
        this.props.navigate('./' + this.viewsId.singleFileView);
      }
    );
  };

  onDelete = (id) => {
    return this.http.DeletFile(id);
  };

  SwitchToCreateFile = () => {
    this.setState(
      {
        viewId: this.viewsId.createFile
      },
      () => {
        this.props.navigate('./' + this.state.viewId);
      }
    );
  };

  CreateFile = (file, form) => {
    return new Promise((resolve) => {
      const payload = {
        Name: form.Name,
        Platforms: form.Platform,
        ShopProduct: form.ProductId,
        Require: form.Required,
        Type: form.Type,
        Version: form.Version
      };
      console.log(file, form);
      this.http.CreateFile(file, payload, (loaded, total) => {
        this.setState({
          uploadFile: {
            loaded: loaded,
            total: total
          }
        });

        if (loaded === total) {
          this.setState(
            {
              uploadFile: {
                loaded: 0,
                total: -1
              }
            },
            () => {
              resolve();
            }
          );
        }
      });
    });
  };

  render = () => (
    <React.Fragment>
      <Routes>
        <Route
          path={`/${this.viewsId.filesView}`}
          element={
            <ShowUpAnimated>
              <FilesView
                swtichToCreate={this.SwitchToCreateFile}
                payload={this.state.filesState.filesPayload}
                onClick={this.onClick}
              />
            </ShowUpAnimated>
          }
        />
        <Route
          path={`/${this.viewsId.singleFileView}`}
          element={
            <ShowUpAnimated>
              <SingleFileView
                onDelete={this.onDelete}
                fileInfo={this.state.singleFileData.fileData}
              />
            </ShowUpAnimated>
          }
        />
        <Route
          path={`/${this.viewsId.createFile}`}
          element={
            <ShowUpAnimated delay={0.2}>
              {!this.props?.products || this.props?.products.length === 0 ? (
                <div className="row">
                  <div className="col-12 p-5 text-center">هیچ محصولی در فروشگاه نیست !</div>
                </div>
              ) : (
                <CreateFileView
                  products={this.props.products}
                  onSubmit={this.CreateFile}
                  progress={this.state.uploadFile}
                />
              )}
            </ShowUpAnimated>
          }
        />
        <Route path={`/`} element={<Navigate to={'./' + this.viewsId.filesView} />} />
        <Route
          path={`/*`}
          element={<NotFound redirect={this.props.navigate} backLink="../files" />}
        />
      </Routes>
    </React.Fragment>
  );
}

const mapStateToProps = (state) => state;
const NavigateWrapper = (Component) =>
  function CMP(props) {
    const nav = useNavigate();
    return <Component {...props} navigate={nav} />;
  };

export default connect(mapStateToProps)(NavigateWrapper(FilesModule));
