import React from 'react';
import { Routes, Route, useMatch, useNavigate, useLocation, Outlet } from 'react-router-dom';

function RouterWrapper({ Component, path, parent }) {
  const match = useMatch(path);
  const navigate = useNavigate();
  const loc = useLocation();

  const ChangeRoute = (p) => {
    parent.current.classList.remove('shown');
    parent.current.classList.add('hide');
    setTimeout(() => {
      navigate(p);
      parent.current.classList.remove('hide');
      parent.current.classList.add('shown');
    }, 250);
  };

  return (
    <>
      <Component match={match} redirect={ChangeRoute} locatio={loc} />
      <Outlet />
    </>
  );
}

export class DashboardRouter extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.parent = React.createRef();
  }

  parent = undefined;

  render() {
    return (
      <div className="shown" ref={this.parent}>
        <Routes>
          {!this.props.routerData ? (
            <></>
          ) : (
            this.props.routerData.map(({ path, Component }, index) => {
              return (
                <Route
                  key={`Router-${index - 1}-${index}`}
                  path={path}
                  element={<RouterWrapper parent={this.parent} Component={Component} path={path} />}
                />
              );
            })
          )}
        </Routes>
      </div>
    );
  }
}

export default DashboardRouter;
