import { HttpBody } from 'core/models/networking/bodyType.model';

export class LoginBody extends HttpBody {
  constructor(username, password) {
    super();
    this.UserName = username;
    this.Password = password;
  }

  UserName = '';
  Password = '';

  toJson() {
    return {
      Username: this.UserName,
      Password: this.Password
    };
  }
}

export class LoginResponse {
  token = '';
  constructor({ Token }) {
    this.token = Token;
  }
}
