import CONFIG from 'assets/config/server.config';
import { Buffer } from 'buffer';
import FormatException from 'core/errors/FormatException';
import NotAuthorizedException from 'core/errors/NotAuthorizedException';
import HttpRequest from 'core/networking/http';

export class AuthGuard {
  #token = '';
  #content = {};
  #http = new HttpRequest(CONFIG.CreatePath(''));

  constructor(token) {
    if (!token || typeof token !== 'string') {
      throw new TypeError("'token' should be String but it received " + typeof token);
    }

    if (token === 'none') {
      throw new NotAuthorizedException('Auth token is ' + token);
    }

    this.#token = token;
    try {
      const json = Buffer.from(token.split('.')[1], 'base64').toJSON();
      this.#content = json;
    } catch (e) {
      console.log(e);
      throw new FormatException("'token' should be String but it received " + typeof token);
    }
  }

  async ValidateToken() {
    this.#http.SetHeader('authorization', this.#token);
    try {
      await this.#http.Get('admin/token/check');
      return true;
    } catch (e) {
      console.log(e);
      throw new NotAuthorizedException();
    }
  }
}
