import StatisticsActions from './actions';

const initialState = {
  users: null,
  shop: null
};

export function StatisticsReducer(state = initialState, action) {
  switch (action.type) {
    case StatisticsActions.SET_USERS:
      return {
        ...state,
        users: action.payload
      };
    case StatisticsActions.SET_SHOP:
      return {
        ...state,
        shop: action.payload
      };
    default:
      return state;
  }
}
export default StatisticsReducer;
